.react-codemirror2 {
  flex: 1 1 auto;
  margin-top: 0;
  height: 100%;
  position: relative;
}

.CodeMirror {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
}

html,
body,
body > div,
.root {
  height: 100%;
  overflow: hidden; /* Hide scrollbars */
}
